body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.list-wrapper {
  width: 80%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  float: right;
  justify-content: center;
}

.list-wrapper img {
  padding-top: 50px;
}

.card {
  width: 25%;
  padding: 15px;
}
.card li {
  list-style: none;
} 

.card h2 {
  text-align: center;
}

hr {
  border: none;
  height: 1px;
  background-color: #000;
}

.card-image img {
  width: 100%;
  height: 150px;
}

.sidebar {
  display: inline-block;
  width: 20%;
  background-color: #F1F1F1;
  position: fixed;
  padding: 15px;
}

.nav-wrapper{
  width: 100%;
  height: 50px;
  background-color: #000;
  padding-top: 20px;
  padding-left: 15px;
}

.nav-wrapper a {
  text-decoration: none;
  font-weight: 600;
  font-size: 24px;
  color: #fff;
}

.bar-item {
  text-align: center;
}

.translations li {
  padding-top: 5px;
  padding-left: 30px; 
  line-height: 30px;
}

.btn {
  background-color: #000;
  color: #fff;
  width: 150px;
  height: 30px;
  border: none;
  border-radius: 5px;
  font-size: 14px;
  font-weight: 600;
  padding: 5px;
  margin-left: 50px;
}

.btn:hover {
  background-color: #F1F1F1;
  color: #000;
  border: 1px solid #000;
}


